<template>
  <section class="slider-two mt-2">
    <div class="container mx-auto p-1">
      <div class="w-full h-auto">
        <div class="w-full mx-auto flex flex-row align-center justify-between">
          <swiper :modules="modules" :slides-per-view="6" :space-between="50" :navigation="true">
            <swiper-slide v-for="items in list" :key="items" class="border-1 hover:border-red-500 rounded-sm">
              <div class="border border-1 hover:border-red-500 p-2 realtive">
                <div class="off w-24 h-6 border bg-red-500 border-1 border-white absolute">
                  <span class="text-white font-thin mr-2.5" :style="{fontSize:Allsize+'px'}">{{items.off}}</span>
                </div>
                <img :src="`${items.img}`" alt="" class="w-full h-auto">
               <p class="text-slate-600 font-thin text-center my-1 overflow-hidden" :style="{fontSize:Allsize+'px',height:myHeight+'px'}">{{items.title}}</p>
                <div class="flex flex-row align-center justify-between bg-red-500 p-2 w-full">
                  <div class="w-10/12 justify-start">
                   <p class="text-white text-center text-sm font-thin p-1" :style="{fontSize:size+'px'}">تومان{{items.price}}</p>
                  </div>
                  <div class="w-2/12"><font-awesome-icon :icon="['fas','eye']" class="text-thin text-white" :style="{fontSize:size+'px'}"></font-awesome-icon></div>
                </div>
              </div>
            </swiper-slide>
          </swiper>
        </div>

      </div>
    </div>
  </section>
</template>

<script>
import{Swiper,SwiperSlide} from 'swiper/vue';
import SwiperCore , {Navigation} from 'swiper'
SwiperCore.use([Navigation])
import 'swiper/css'
import 'swiper/css/navigation'
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import{ref} from 'vue'
export default {
  name: "ExampleComponent",
  components:{Swiper,SwiperSlide,FontAwesomeIcon},
  props:{
    list:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const size=ref(15)
    const Allsize=ref(12)
    const myHeight=ref(40)
    return{
      size,
      myHeight,
      Allsize,
      modules:[Navigation]
    }
  }
}
</script>

<style scoped>
.swiper-slide{
  margin-left:15px!important;
  overflow: hidden;
}
.off{
  transform: rotate(45deg);
  top: 15px;
  right: -15px;
}
</style>